.cmo-graph {
  text-align: center;

  &__item {
    font-size: .9rem;

    h3 { font-weight: 400; }
  }

  &__image-wrapper {
    border-radius: 50%;
    max-height: 150px;
    max-width: 150px;
    padding: 20px;
    border: 3px solid rgba(black, .2);
    box-shadow: 2px 2px 5px rgba(black, .1);
    overflow: hidden;
    margin: 0 auto;
    transition: all .4s ease;

    img { width: 100%; }
  }

  &__item__text {
    text-align: center;
    h3 {margin-bottom: 5px;}
    p { 
      color: rgba(black, .5); 
      margin: 0;
    }
  }

  &__item--false,
  &__item--true {
    .cmo-graph__status { 
      position: relative; 
      display: inline-block;
    }

    .cmo-graph__status:after {
      @extend .u-material-icons;
      width: 38px;
      height: 38px;
      position: absolute;
      right: 0;
      top: 100px;
      font-size: 2rem;
      color: white;
      border-radius: 50%;
      padding: 3px;
    }
  }

  &__item--true {
    color: $green;

    .cmo-graph__image-wrapper { border-color: $green; }
    .cmo-graph__status:after {
      background-color: $green;
      content: 'check';
    }
  }

  &__item--false {
    color: $red;

    .cmo-graph__image-wrapper { border-color: $red; }
    .cmo-graph__status:after {
      background-color: $red;
      content: 'close';
    }
  }
}

.tooltip-wrapper,
.cmo-graph__item {
  position: relative;
  cursor: pointer;

  &:hover .cmo-graph__image-wrapper { 
    border-color: $main-color; 
    transition: all .7s ease;
  }
  &:hover .tooltip { 
    opacity: 1; 
    transition: all .7s ease;
    top: calc(100% + 20px);
  }

}

.tooltip {
  width: 300px;
  padding: 10px 20px;
  position: absolute;
  left: -60px;
  top: calc(100% + 40px);
  opacity: 0;
  background-color: white;
  border: 3px solid $main-color;
  box-shadow: 2px 2px 5px rgba(black, .2);
  transition: all .4s ease;

  &:before {
    content: '';
    width: 0;
    height: 0;
    border: 16px solid transparent;
    border-bottom-color: $main-color;
    position: absolute;
    top: -32px;
    left: calc(50% - 16px);
  }

  h3 { color: $main-color; }

}